import React, {Comnponent} from 'react'

class Frm extends Component{
    constructor(props){
        super(props);
    }

    multiplicar = (evento) => {
        var v1 = parseInt(evento.valor1.value, 10);
        var v2 = v1 * 1000;
        alert(`el calulo es: ${v1} * 1000 = ${v2}`);
    }

    render(){
        return (
            <div>
                <form onSubmit={this.multiplicar}>
                    <p><input name="valor1" type="text"/></p>
                    <p><input value="Calcular" type="submit"/></p>
                </form>
            </div>
        )
    }
}

export default Frm;